* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
  overflow: hidden;
}

form {
  max-width: 400px;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

form p {
  color: red;
}

footer {
  text-align: center;
  font-size: 12px;
}

footer a {
  text-decoration: none;
  color: black;
}

.App {
  display: flex;
  min-height: 100vh;
  min-width: 100vw;
}

.left {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #F9F9F9;
}

.left-inner {
  width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 100%;
  position: relative;
}

.left-inner h1 {
  font-size: 2.375rem;
  text-transform: uppercase;
  color: #FF8000;
  line-height: 1;
  font-weight: 900;
}

.left-inner h2 {
  margin-bottom: 1.25rem;
  font-weight: 500;
}

.right {
  overflow: hidden;
  display: none;
  background-size: cover;
}

.right img {
  min-height: 145%;
  min-width: 100%;
}

.logo {
  display: flex;
  gap: 1rem;
  align-items: center;
  width: min-content;
  margin: 0 auto;
}

.logo img {
  height: 76px;
}

.buttons {
  display: flex;
  gap: 0.5rem;
  align-self: flex-end;
}

@media screen and (min-width: 640px) and (min-height: 430px) {
  .right {
    flex-grow: 1;
    display: inline;
    background-position-x: 15%;
  }

  .left {
    width: 50%;
  }
}

@media screen and (min-width: 1281px) {
  .left {
    width: 25%;
  }

  .right {
    background-position-x: 0;
    background-size: 180%;
  }
}
